import React from 'react';
import './App.css';
import { BrowserRouter as Router, Switch, Route, Redirect } from 'react-router-dom';
import LoginComponent from '../components/pages/onboarding/login/LoginPageComponent';
import { connect } from "react-redux";
import { CssBaseline, createMuiTheme, ThemeProvider } from '@material-ui/core';
import OnBoardingLayout from '../components/pages/onboarding/onboarding-layout/OnBoardingLayout';
import AddCompanyComponent from '../components/pages/onboarding/company/AddCompanyComponent';
import ProductCategory from '../components/pages/main/product-category/ProductCategory'
import ProtectedRoute from '../router/ProtectedRoute';
import MainLayoutComponent from '../components/pages/main/main-layout/MainLayoutComponent';
import LoadingOverlay from 'react-loading-overlay';
import MainProtectedRoute from '../router/MainProtectedRoute';
import Employees from '../components/pages/main/employee/Employees';
import  Product  from '../components/pages/main/product/Product';
import Location from '../components/pages/main/location/Location';
import { CollectionsOutlined } from '@material-ui/icons';


const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#333996",
      light: '#3c44b126'
    },
    secondary: {
      main: "#f83245",
      light: '#f8324526'
    },
    background: {
      default: "#f4f5fd"
    },
  },
  overrides: {
    MuiAppBar: {
      root: {
        transform: 'translateZ(0)'
      }
    }
  },
  props: {
    MuiIconButton: {
      disableRipple: true
    }
  }
})


class App extends React.Component {
  state = { selectedItem: 'Tickets' };
  isAuthenticated = false;
  isHaveCompany = true;
  constructor(props) {

    super();

    if (props.cacNumber !== null) {
      if (props.cacNumber !== undefined) {
        this.isAuthenticated = true
        console.log(props.cacNumber)
        if (props.cacNumber === "00000") {
          this.isHaveCompany = false
        }
      }
    }


  }



  componentDidMount() {

    window.addEventListener('resize', this.resize);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resize);
  }


  resize = () => this.forceUpdate();

  render() {
    return (

      <ThemeProvider theme={theme}>
        <Router>
          <Switch>
            <Route path="/user" component={OnBoardingLayout} />
            <ProtectedRoute
              isAuthenticated={this.isAuthenticated}
              path="/add-company"
              component={AddCompanyComponent}
            />
            <LoadingOverlay
              active={this.props.spinner}
              spinner
            >
              <MainProtectedRoute isAuthenticated={this.isAuthenticated} isHaveCompany={this.isHaveCompany} path='/main/dashboard' component={Employees} />
              <MainProtectedRoute isAuthenticated={this.isAuthenticated} isHaveCompany={this.isHaveCompany} path='/main/product-category' component={ProductCategory} />
              <MainProtectedRoute isAuthenticated={this.isAuthenticated} isHaveCompany={this.isHaveCompany} path='/main/product' component={Product} />
              <MainProtectedRoute isAuthenticated={this.isAuthenticated} isHaveCompany={this.isHaveCompany} path='/main/locations' component={Location} />
            </LoadingOverlay>

            <Route exact path="/" render={() => (
              this.props.triggerLogging === true ? <Redirect to="/" /> : <LoginComponent />
            )} />

          </Switch>
        </Router>
        <CssBaseline />
      </ThemeProvider>
    );
  }


}

function mapStateToProps(state) {
  return {
    cacNumber: state.userReducer.cacNumber,
    user: state.userReducer.user,
    buttonloader: state.utilityReducer.buttonloader,
    spinner: state.utilityReducer.spinner
  };
}
const mapDispatchToProps = (dispatch) => ({

  SetLoader() {
    dispatch({ type: "LOADING_BUTTON_SPINNER", });
  },

})


export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);